    display: flex;
    flex-direction: column;
    gap: 10px;
    max-width: 140px;
    color: var(--text-white);
    font-weight: 300;
    font-size: 10px;
    font-family: 'MontserratLight', sans-serif;

    img {
      width: 140px;
      height: 24px;
